<template>
  <div class="product_body">
    <div class="top_banner">
      <img src="@/assets/hangyeyingyong.png">
    </div>

  </div>
</template>
<script>
import Vue from "vue";
import { mapGetters } from "vuex";
import "@/styles/ProductModel.css";
import typeproduct from "./components/TypeProduct";

export default {
  name: "ProductModel",
  components: {
    "com-product": typeproduct
  },
  computed: {
    ...mapGetters(["machine"])
  },
  data() {
    return {
      products: null,
      isShow: false,
      aa: null,
      typeShow: [
        // 12个
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false
      ]
    };
  },
  mounted() {
    let vm = this;
    setTimeout(() => {
      vm.getDataList(vm.machine);
    }, 500);
  },
  updated() {
    let vm = this;
    setTimeout(() => {
      vm.getDataList(vm.machine);
    }, 500);
  },

  methods: {
    //将vuex中产品全部数据按产品名称分类
    getDataList(machine) {
      let vm = this;
      let productContent = machine;
      productContent = productContent
        .map(function (item, index, arr) {
          const i = productContent.find(_item => item.typeId === _item.typeId);
          if (i !== item) {
            i.detail.push({
              typeId: item.typeId,
              typeName: item.typeName,
              productId: item.productId,
              productName: item.productName,
              img: item.img,
              productMaterial: item.productMaterial,
              market: item.market,
              characteristic: item.characteristic,
              detailsImg: item.detailsImg,
              dataTableImg: item.dataTableImg,
              product_description: item.product_description,
            });
            return undefined;
          } else {
            i.detail = [
              {
                typeId: item.typeId,
                typeName: item.typeName,
                productId: item.productId,
                productName: item.productName,
                img: item.img,
                productMaterial: item.productMaterial,
                market: item.market,
                characteristic: item.characteristic,
                detailsImg: item.detailsImg,
                dataTableImg: item.dataTableImg,
                product_description: item.product_description,
              }
            ];
            return i;
          }
        })
        .filter(item => item !== undefined);

      productContent.forEach(element => {
        element["isShow"] = false;
      });
      this.products = productContent;
    },
    showTotal(indexTemp) {
      Vue.set(this.typeShow, indexTemp, true);
    },
    hideTotal(indexTemp) {
      Vue.set(this.typeShow, indexTemp, false);
    }
  },
  watch: {
    typeShow(newValue, oldValue) {
      //
    }
  }
};
</script>
<style scoped>
</style>

<style>
</style>





