<template>
  <div class="product_views">
    <div class="product_title">
      <p>
        {{item.typeName}}
        <span v-show="item.detail.length > 4 ">
          <i class="el-icon-caret-top" v-show="!isShow"></i>
          <i class="el-icon-caret-bottom" v-show="isShow"></i>
        </span>
      </p>
      <img src alt>
    </div>
    <ul>
      <li
        class="product_img"
        v-for="(product,productIndex) in item.detail.slice(0,4)"
        :key="productIndex  + 'b'"
        @click="goDetails('/detailsintroduce',item.detail[productIndex])"
      >
        <img :src="product.img">
        <!-- 遮罩 -->
        <div class="show_product_name">
          <P>{{product.productName}}</P>
        </div>
        <!-- 遮罩 -->
      </li>
      <li
        class="product_img"
        v-for="(product,index2) in item.detail.slice(5)"
        :key="index2 + 'c'"
        @click="goDetails('/detailsintroduce',item.detail[index2])"
        v-show="isShow"
      >
        <img :src="product.img">
        <div class="show_product_name">
          <P>{{product.productName}}</P>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import "@/styles/ProductModel.css";

export default {
  name: "TypeProduct",
  props: ["item", "isShow"],
  data() {
    return {
    };
  },
  methods: {
    goDetails(link, item) {
      let vm = this;
      vm.$store
        .dispatch("set_currentProduct2", item)
        .then(function(response) {
          vm.$router.push({
            path: link
          });
        })
        .catch(function(error) {
          console.info(error);
        });
    }
  },
  mounted() {

  }
};
</script>
<style scoped>
</style>

<style>
</style>





